import { useEffect, useState } from 'react';
import Circle from "react-circle";
import Search from "./Search"
import Notice from "./Notice"
import { IconButton, Button } from '@material-ui/core';
import AlarmIcon from '@material-ui/icons/Alarm';
import { BsStopwatch } from "react-icons/bs"

import Model from "./Modal"

export default function Circlem() {
    //進捗率
    const [progress, setProgress] = useState(0);

    //レンダリング時に進捗率をセット
    useEffect(() => {
        setProgress(80)
    }, []);

    return (
        <div style={{ marginTop: "50px", textAlign: "center" }}>
            <header className="w-full sticky top-0 flex justify-center bg-gray-300">
                <Notice />
                <Search />
            </header>
            <Circle
                progress={progress}           //進捗率
                animationDuration="1s"        //アニメーション時間
            />
            <div className="m-4">
                <div className="rounded-3xl shadow-2xl h-20 w-full p-4  bg-gray-200">
                </div>
            </div>
            <div className="m-4">
                <div className="rounded-3xl shadow-2xl h-20 w-full p-4  bg-gray-200">
                </div>
            </div>
            <div className="m-4">
                <div className="rounded-3xl shadow-2xl h-20 w-full p-4  bg-gray-200">
                </div>
            </div>
            <div className="m-4">
                <div className="rounded-3xl shadow-2xl h-20 w-full p-4  bg-gray-200">
                </div>
            </div>
            <div className="m-4">
                <div className="rounded-3xl shadow-2xl h-20 w-full p-4  bg-gray-200">
                </div>
            </div>
            <div className="m-4">
                <div className="rounded-3xl shadow-2xl h-full w-full p-4  bg-gray-200">
                    <div className="rounded-xl bg-blue-300 h-12 w-12"></div>
                    <Button variant="contained" color="red">
                        <AlarmIcon />
                    </Button>
                    <IconButton color="inherit" aria-label="add an alarm">
                        <AlarmIcon />
                    </IconButton>
                    <IconButton color="primary" aria-label="add an alarm" className="">
                        <BsStopwatch />
                    </IconButton>
                    <Model />
                </div>
            </div>
        </div>
    );
}

